.cabecera {
    width: 100%;
    height: 10vh;
    background-color: rgb(17, 17, 17);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.cabecera img {
    width: 12vw;
    height: auto;
    margin: 0 0 0 2vw;
}

.cabecera div {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 2vw 0 0;
    gap: 1.736vw;
}

.cabecera link {
    height: 100%;
}
